.CreateTodoButton {
  background-color: #61DAFA;
  -webkit-box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
          box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed; /*sticky;*/
  left: 50%;
  left: calc(50% - 30px);
  bottom: 24px;

  font-weight: bold;
  color: #FAFAFA;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 64px;
  width: 64px;
  margin: 0px auto;
  -webkit-transform: rotate(0);
      -ms-transform: rotate(0);
          transform: rotate(0);
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  /*z-index:1; */  /* esto permite que siempre esté visible*/
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
}



